body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 8vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.BackgroundImage{			
    position: relative;
    
}		

h1 {
    text-align:center;
}

.BackgroundImage > img{
	width: 85%;
	height:auto;
	scale: 0.50;
}

.video-responsive {
  	margin: 8px;
  	padding-top: 10px;
	overflow: hidden;
	/*box-shadow: 0px 6px 18px -9px rgba(0,0,0,0.75);*/
	transition: -webkit-transform 100ms ease-in;
	transition: transform 100ms ease-in;
	transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
	cursor: pointer;
	margin-top: 80px;
	width: auto;
	height: auto;
	text-align: center;

}
.video-responsive > iframe{
	margin-bottom: 20px;
	border-radius: 20px;
	display: block;
	margin-left: auto;
  	margin-right: auto;
  	width: 55%;
  	object-fit: cover;
  	min-width: 100px;
  	min-height: 100px;

}


.homepage{
	padding-top: 90px;
}
.background_video{
	padding-top: 20px;
	background-color: #DCDCDC;
}
.navbar{
	position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
}
.aboutpage {
	margin-top: 100px;
	padding: 30px;
	text-align: center;

}.aboutpage >h5{
	padding-top: 30px;
	padding-bottom: 30px;
}.aboutpage >h2{
	border-radius: 10px;
	padding-top: 30px;
}
.about_officers{
	padding-top: 30px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
}
.about_members{
	padding-top: 30px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
}
.alumni_dropdown{
	padding-top: 30px;]
	background-color: red;
}
.profile{
	margin: 10px;
	border-radius: 10px;
	overflow: hidden;
	box-shadow: 0px 6px 18px -9px rgba(0,0,0,0.75);
	transition: -webkit-transform 100ms ease-in;
	transition: transform 100ms ease-in;
	transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
	cursor: pointer;
	margin-top: 100px;
	width: auto;
	height: auto;
	text-align: center;
}

.profile:hover{
	-webkit-transform: scale(1.07);
	        transform: scale(1.07);
}

.profile > img {
	margin-bottom: 50px;
	border-radius: 12px;
	display: block;
	margin-left: auto;
  	margin-right: auto;
  	width: 45%;
  	object-fit: cover;
  	min-width: 100px;
  	min-height: 100px;


}

.profile{
